<template>
  <div>
    <p></p>
    <v-button></v-button>
    <v-button type="primary"></v-button>
    <v-button type="text"></v-button>

    <p></p>
    <v-button disabled></v-button>
    <v-button type="primary" disabled></v-button>
    <v-button type="text" disabled></v-button>

    <p></p>
    <v-button type="success"></v-button>
    <v-button type="warning"></v-button>
    <v-button type="danger"></v-button>
    <v-button type="info"></v-button>

    <p></p>
    <v-button type="primary" loading></v-button>
    <v-button :loading="loading1" @click="toLoading1">
      <span v-if="!loading1"></span>
      <span v-else>...</span>
    </v-button>
    <v-button type="primary" :loading="loading2" @click="toLoading2">
      <span v-if="!loading2"></span>
      <span v-else>...</span>
    </v-button>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        loading1: false,
        loading2: false
      }
    },
    methods: {
      toLoading1() {
        this.loading1 = true;

        setTimeout(() => {
          this.loading1 = false;
        }, 1000);
      },
      toLoading2() {
        this.loading2 = true;

        setTimeout(() => {
          this.loading2 = false;
        }, 1000);
      }
    }
  };
</script>

<style lang="scss" scoped>
  button {
    margin: 2px 5px;
  }
</style>


