<template>
  <div>
    <p></p>
    <v-button @click="openNotification1"></v-button>
    <v-button @click="openNotification2"></v-button>
    <v-button @click="openNotification7"></v-button>

    <p></p>
    <v-button
      type="success"
      @click="openNotification3"></v-button>
    <v-button
      type="warning"
      @click="openNotification4"></v-button>
    <v-button
      type="danger"
      @click="openNotification5"></v-button>
    <v-button
      type="info"
      @click="openNotification6"></v-button>
  </div>
</template>

<script>
  export default {
    methods: {
      openNotification1() {
        this.$notify({
          title: '',
          message: '',
        });
      },

      openNotification2() {
        this.$notify({
          title: ''
        });
      },

      openNotification3() {
        this.$notify({
          title: '',
          message: '',
          type: 'success'
        });
      },

      openNotification4() {
        this.$notify({
          title: '',
          message: '',
          type: 'warning'
        });
      },

      openNotification5() {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        });
      },

      openNotification6() {
        this.$notify({
          title: '',
          message: '',
          type: 'info'
        });
      },

      openNotification7() {
        this.$notify({
          title: '',
          message: '',
          duration: 0
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
  button {
    margin: 2px 5px;
  }
</style>


