<template>
  <div>
    <p></p>
    <v-button @click="openMessage"></v-button>

    <p></p>
    <v-button
      type="success"
      @click="openSuccessMessage"></v-button>
    <v-button
      type="warning"
      @click="openWarnMessage"></v-button>
    <v-button
      type="danger"
      @click="openFailMessage"></v-button>
    <v-button
      type="info"
      @click="openInfoMessage"></v-button>
  </div>
</template>

<script>
  export default {
    methods: {
      openMessage() {
        this.$message({
          message: ''
        });
      },

      openSuccessMessage() {
        this.$message({
          message: '',
          type: 'success'
        });
      },

      openWarnMessage() {
        this.$message({
          message: '',
          type: 'warning'
        });
      },

      openFailMessage() {
        this.$message({
          message: '',
          type: 'error'
        });
      },

      openInfoMessage() {
        this.$message({
          message: '',
          type: 'info'
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
  button {
    margin: 2px 5px;
  }
</style>


