<template>
  <div>
    <p></p>
    <v-radio v-model="checked" label="orange"></v-radio>
    <p class="result">{{ checked }}</p>
    
    <p></p>
    <v-radio v-model="fruit" label="apple"></v-radio>
    <v-radio v-model="fruit" label="banana"></v-radio>
    <v-radio v-model="fruit" label="pear"></v-radio>
    <p class="result">{{ fruit }}</p>

    <p></p>
    <v-radio-group v-model="city">
      <v-radio label="beijing"></v-radio>
      <v-radio label="shanghai"></v-radio>
      <v-radio label="guangzhou"></v-radio>
      <v-radio label="shenzhen"></v-radio>
    </v-radio-group>
    <p class="result">{{ city }}</p>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        checked: '',
        fruit: 'banana',
        city: 'shanghai'
      };
    }
  };
</script>

<style lang="scss" scoped>
  label {
    margin: 0 5px;
  }

  .result {
    color: #20a0ff;
  }
</style>


