<template>
  <div>
    <p></p>
    <v-button @click="visible = true;"></v-button>
    <v-dialog v-model="visible">
      <span></span>
      <span slot="footer">
        <v-button @click="visible = false"> </v-button>
        <v-button type="primary" @click="visible = false"> </v-button>
      </span>
    </v-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        visible: false
      };
    },

    watch: {
      visible(val) {
        console.log('watch -- ', val);
      }
    }
  };
</script>

<style lang="scss" scoped>
  button {
    margin: 2px 5px;
  }
</style>


