<template>
  <div>
    <p></p>
    <v-select v-model="select">
      <v-option label="" value="Beijing"></v-option>
      <v-option label="" value="Shanghai"></v-option>
      <v-option label="" value="Guangzhou"></v-option>
      <v-option label="" value="Shenzhen"></v-option>
    </v-select>
    <p class="result">{{ select }}</p>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        select: 'Beijing'
      };
    }
  };
</script>

<style lang="scss" scoped>
  .result {
    color: #20a0ff;
  }
</style>


