<template>
  <div>
    <p></p>
    <v-checkbox v-model="checked" label="orange"></v-checkbox>
    <p class="result">{{ checked }}</p>

    <p></p>
    <v-checkbox v-model="once" label="orange"></v-checkbox>
    <p class="result">{{ once }}</p>

    <p></p>
    <v-checkbox v-model="fruit" label="apple"></v-checkbox>
    <v-checkbox v-model="fruit" label="banana"></v-checkbox>
    <v-checkbox v-model="fruit" label="pear"></v-checkbox>
    <p class="result">{{ fruit }}</p>

    <p></p>
    <v-checkbox-group v-model="city">
      <v-checkbox label="beijing"></v-checkbox>
      <v-checkbox label="shanghai"></v-checkbox>
      <v-checkbox label="guangzhou"></v-checkbox>
      <v-checkbox label="shenzhen"></v-checkbox>
    </v-checkbox-group>
    <p class="result">{{ city }}</p>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        checked: false,
        once: [],
        fruit: [],
        city: []
      }
    }
  };
</script>

<style lang="scss" scoped>
  label {
    margin: 0 5px;
  }

  .result {
    color: #20a0ff;
  }
</style>


